import React from "react";
import Box from "@mui/material/Box";
import Grid from "@mui/material/Grid";
import Paper from "@mui/material/Paper";
import Typography from "@mui/material/Typography";

export default function StatusTabs() {
  return (
    <Box
      sx={{
        m:1,
        display: "flex",
        height: "fit-content",
      }}
    >
      <Grid
        container
        justifyContent="space-around"
        alignItems="flex-start"
      >
        <Grid item xs={12} md={6} lg={3}>
          <Paper sx={{ textAlign: "center", backgroundColor: "primary.dark", }}>
            <Typography variant="h6">Total Value</Typography>

            <Typography variant="h5" sx={{fontWeight:"bold"}}>80</Typography>
          </Paper>
        </Grid>
        <Grid item xs={12} md={6} lg={3}>
          <Paper sx={{ textAlign: "center", backgroundColor: "primary.dark" }}>
            <Typography variant="h6" >Investment</Typography>

            <Typography variant="h5" sx={{fontWeight:"bold"}}>70</Typography>
          </Paper>
        </Grid>
        <Grid item xs={12} md={6} lg={3}>
          <Paper sx={{ textAlign: "center", backgroundColor: "primary.dark" }}>
            <Typography variant="h6">Funds Available</Typography>

            <Typography variant="h5" sx={{fontWeight:"bold"}}>30</Typography>
          </Paper>
        </Grid>
      </Grid>
    </Box>
  );
}
